.prices__elements {
    width: 100vw;
    margin: 0;
    z-index: 0;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}
.prices__element {
    opacity: 1;
}
.prices__left-curly-brackets {
    transform: matrix(
        0.72159603080403,
        0.37432663296015,
        -0.37432663296015,
        0.72159603080403,
        0,
        0
    );
    left: 6.406%;
    top: 17.333%;
}
@keyframes moveLeftCurlyBracketsInPricesSection {
    0%,
    100% {
        opacity: 1;
        left: 6.406%;
        top: 17.333%;
    }
    10% {
        opacity: 0.5;
    }
    50% {
        opacity: 0;
        left: 50%;
        top: 50%;
    }
}
.prices__hashtag {
    transform: matrix(
        0.91532249290851,
        0.26083794888422,
        -0.26083794888422,
        0.91532249290852,
        0,
        0
    );
    left: 12.708%;
    top: 70.438%;
}
