    }
    50% {
        opacity: 0;
        left: 50%;
        top: 50%;
    }
}
.prices__brackets-with-slash {
    transform: matrix(
        0.62913254694498,
        -0.25657231186215,
        0.25657231186215,
        0.62913254694498,
        0,
        0
    );
    left: 85.573%;
    top: 19.802%;
}
@keyframes moveBracketsInPricesSection {
