        0.25151098414347,
        0.79372553245355,
        0,
        0
    );
    left: 91.7%;
    top: 71%;
}
@keyframes moveRightCurlyBracketsInPricesSection {
    0%,
    100% {
        opacity: 1;
        left: 91.7%;
        top: 71%;
    }
    10% {
        opacity: 0.5;
    }
    50% {
        opacity: 0;
        left: 50%;
        top: 50%;
    }
}
