    transform: matrix(
        0.58798214978162,
        0.21554688540119,
        -0.21554688540119,
        0.58798214978162,
        0,
        0
    );
    left: 72.704%;
    top: 82%;
}
@keyframes moveDivInPricesSection {
    0%,
    100% {
        opacity: 1;
        left: 72.704%;
        top: 82%;
    }
    10% {
        opacity: 0.5;
    }
    50% {
        opacity: 0;
        left: 50%;
        top: 50%;
    }
}
.prices__elements:hover .prices__left-curly-brackets {
    animation: moveLeftCurlyBracketsInPricesSection 2s ease-out forwards;
}
.prices__elements:hover .prices__hashtag {
    animation: moveHashtagInPricesSection 2s ease-out forwards;
}
.prices__elements:hover .prices__brackets-with-slash {
    animation: moveBracketsInPricesSection 2s ease-out forwards;
}
.prices__elements:hover .prices__right-curly-brackets {
    animation: moveRightCurlyBracketsInPricesSection 2s ease-out forwards;
}
.prices__elements:hover .prices__div-tag {
    animation: moveDivInPricesSection 2s ease-out forwards;
}

